$backround: rgb(235, 226, 209);
$black: rgb(60, 60, 60);
$white: rgb(247, 244, 234);
$purple: blueviolet;
$dark-purple: rgb(98, 28, 164);


html {
    font-family:'Courier New', Courier, monospace;
    font-size: 16px;
    color: $black;
}

body {
    height: 100vh;
    margin: 0;
    padding: 0;
    background: $backround;
    * {
        margin: 0;
    }
}

h1 {
    font-size: 3rem;
}

img {
    display: block;
}

.container {
    display: flex;
    flex-flow: column;
    padding: 0rem 2rem;
    height: 100%;
    color:bl
}

.container-small {
    padding: 2rem 6rem;
}

ul {
    list-style-type: none;
}

button {
    height: 2rem;
    width: 6rem;
    color: $purple;
    letter-spacing: 1px;
    // font-weight: 600;
    background-color: $white;
    border: 1px solid $purple;
    transition: all 200ms ease-in-out;

    &:hover {
        color: $white;
        background-color: $dark-purple;
        border: 1px solid $white;
    }
}

a {
    text-decoration: none;
    color: $black;
    transition: all 200ms ease-in-out;

    &:hover {
        color: $purple;
    }
}

.iframe-container {
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
}
iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 50%;
    height: 50%;
}